/* collapsed sidebar styles */
@media screen and (max-width: 767px) {
  .block-offcanvas {
    position: relative;
  }
  .block-offcanvas,
  .sidebar-offcanvas {
    -webkit-transition: all 0.25s ease-out;
    -moz-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
    left: 0;
  }
  .block-offcanvas-left .sidebar-offcanvas {
    left: -45%;
  }
  .block-offcanvas-left.active {
    left: 45%;
  }
  /* this really should not go here, it's the wrong place but oh well */
  .block-offcanvas div.view {
    padding-left: 0!important;
  }
  .block-offcanvas.active div.view {
    padding-left: 10px!important;
  }
  .block-offcanvas nav {
    opacity: 0!important;
  }
  .block-offcanvas.active nav {
    opacity: 1 !important;
  }
  .sidebar-offcanvas {
    position: absolute;
    top: 0;
    width: 45%;
  }
}
